
.contenedorshorten {
 background-image: url(https://firebasestorage.googleapis.com/v0/b/parcialreactimg.appspot.com/o/bg-shorten-desktop.svg?alt=media&token=1a91b3dc-5c87-4df3-87ba-5e9b7364e2fd);
 background-repeat: no-repeat;
 background-color:var(--darkviolet);
 width: 1000px;
 padding: 45px 55px;
 border-radius: 8px;
}

.contenedorshorten input{
    width: 760px;
    height: 30px;
    padding:  10px 0px 10px 30px;
    border-radius: 10px;
}

.contenedorshorten button{
    width: 180px;
    height: 55px;
    margin-left: 20px;
    background-color: var(--cyan);
    border-radius: 10px;
    font-family: 'PoppinsBold';
    color: white;
    font-size: 18px;
}

.infoShorten{
    margin: 10px 30px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.infoShorten p{
    font-family: 'PoppinsRegular';
}

.infoShorten a{
    font-family: 'PoppinsRegular';
    color: var(--cyan);
    text-decoration: none;
}

.infoShorten textarea{
    border: 0px;
    font-family: 'PoppinsRegular';
    color: var(--cyan);
    overflow: none;
    resize: none;
}

.infoShorten button{
    width: 90px;
    height: 35px;
    margin-left: 20px;
    background-color: var(--cyan);
    border-radius: 5px;
    font-family: 'PoppinsBold';
    color: white;
    font-size: 14px;
    border: 0px;
}

.buttonchoice{
    width: 90px;
    height: 35px;
    margin-left: 20px;
    background-color: var(--cyan);
    border-radius: 5px;
    font-family: 'PoppinsBold';
    color: white;
    font-size: 14px;
    border: 10px;
}

.der{
    margin: 18px 0;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 10px;
}

.linkcorto{
    grid-area: 1/1/1/2;
}

.btncopy{
    grid-area: 1/2/1/3;
}

