:root{
  --cyan:#2ACFCF;
  --cyanclaro:#81dbdb;
  --darkviolet:#3A3054;
  --red:#F46161;
  --gray:#BFBFBF;
  --grayisviolet:#9D9AA7;
  --darkblue:#35313E;
  --ultradarkviolet:#222026;
 }
 

.Characteristics{
  display: grid;
  grid-template-columns: 0.5fr 0.2fr 0.5fr 0.2fr 0.5fr;
  grid-template-rows: repeat(4, 7%);
}

#card-1{
  grid-area: 1/1/1/2;
}
.rect-1{
  grid-area: 4/2/4/3;
}
#card-2{
  grid-area: 2/3/2/4;
}
.rect-2{
  grid-area: 4/4/4/5;
}
#card-3{
  grid-area: 3/5/3/6;
}

svg{
  width: 35px; 
  height: 10px; 
  background-color: var(--cyan);
}